.sidebar-footer {
    position: absolute;
    bottom: 0;
    padding-left: 24px;
    padding-bottom: 32px;
    &__mail {
        display: block;
        text-decoration: none;
        font-family: 'TT Lakes';
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px;
        color: $text-color;

        margin-top: 12px;
        margin-bottom: 16px;
        &:hover {
            opacity: 0.8; } }
    &__phone {
        display: block;
        text-decoration: none;
        font-family: 'TT Lakes';
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.6px;
        color: $text-color;
        font-weight: 700;

        margin-bottom: 8px;
        &:hover {
            opacity: 0.8; } }
    &__lang-choice {
        & a {
            font-family: 'TT Lakes';
            font-size: 16px;
            line-height: 24px;
            letter-spacing: -0.2px;
            color: $text-color;

            text-decoration: none;

            margin-right: 7px;
            &:hover {
                color: $sidebar-active-color; }
            &.sidebar-footer__lang-choice--active {
                color: $menu-item-color; } } } }

@media (min-width: 768px) {
    .sidebar-footer {
        &__phone {
            margin-bottom: 40px; } } }
