.main-header {
    display: flex;
    padding-top: 24px;
    justify-content: space-between;
    &__text {
        position: relative;
        margin: auto 0;
        padding-left: 16px;
        h1 {
            margin-top: 0;
            margin-bottom: 0;

            font-family: "TT Lakes";
            font-size: 28px;
            line-height: 40px;
            letter-spacing: -0.6px;
            color: $text-color; }

        &::before {
            display: block;
            content: "";
            background: $highlight-color;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;

            width: 4px;
            height: 40px;

            position: absolute;

            left: 0; } } }

@media (min-width: 1120px) {
    .main-header {
        padding-top: 42px; } }
